nav{
    position: fixed;
    display: flex;
    margin: auto;
    width: 100%;
}
.nav-links{
    position: fixed;
    display: flex;
    list-style:none;
    justify-content: space-between;
    top: 0;
    left:25%;
    width: 10%;
    margin: 5px;
    padding: 0.1rem;
    align-items: center;
    height: 9vh;
}
.nav-links a{
    letter-spacing: 3px;
    margin: 20px;
    cursor: pointer;
    color: #ffffff; 
    padding: 5px;
    text-decoration:none; 
    font-size: 1.0rem;
}
.nav-links a:hover{
    color:#000;
    background-color: #ffffff; 
    padding: 5px;
    text-decoration:none; 
    font-weight: 400;
    border-radius: 10px;
}
footer{
    position: fixed;
    display: flex;
    bottom: 0;
    left: 0;
    width: 100%;
    background-Color: rgb(0,0,0, 0.7);
}
.footer-links{
    color: rgb(255,255,255);
    justify-content: space-between;
    padding: 0.1rem;
    align-items: center;
    height: 5vh;
}
.footer-links h4{ position: absolute; right: 0px;  left: 0px;}
.footerins{position: absolute;   color: rgb(255,255,255);  left: 45%;}
.footercell{position: absolute;  color: rgb(255,255,255);  right: 45%;}

@media(max-width:900px){
    .nav-links{
        position: fixed;
        top: 0%;
        left:0%;
        width:85%;
        padding: 0.3rem 3rem;
        align-items: center;
        height: 2vh;
    }
    .nav-links a{
        
        letter-spacing: 2px;
        margin: 5px;
        cursor: pointer;
        color: #ffffff; 
        padding: 5px;
        text-decoration:none; 
        font-size: 0.9rem;
    }
    .footer-links h4{
        position: fixed;
        right: 0px;  left: 0px;
    }
    .footerins{
        position: fixed;
        left: 20%;
    }
    .footercell{
        position: fixed;
        right: 20%;
    }
    
}
@media(max-width:540px){
    .nav-links{
        padding: 0px;
        align-items: center;
        height: 3vh;
    }
    .nav-links a{
        margin: 2%;
        letter-spacing: 2px;
        cursor: pointer;
        color: #ffffff; 
        padding: 3px;
        text-decoration:none; 
        font-size: 10px;
    }
}