.foto img{
    cursor: pointer;
    width:55%;
    height: 20%;
}
.foto > img {
    position: absolute;
    top:10%;
    left: 23%;
    left: calc(50% - auto);
    opacity: 0;
    animation: display 9s infinite;
    height:auto;
}

img:nth-child(1) {
    animation-delay:0s;
}
img:nth-child(2) {
    animation-delay:20s;
}
img:nth-child(3) {
    animation-delay:40s;
}
img:nth-child(4) {
    animation-delay:60s;
}
img:nth-child(5) {
    animation-delay:80s;
}
img:nth-child(6) {
    animation-delay:120s;
}
img:nth-child(7) {
    animation-delay:140s;
}
img:nth-child(8) {
    animation-delay:160s;
}
img:nth-child(9) {
    animation-delay:180s;
}
img:nth-child(10) {
    animation-delay:220s;
}

@keyframes display {
    0% {
        transform: translateX(50px);
        opacity: 0;
    }
    10% {
        transform: translateX(30px);
        opacity: 0;
    }
    25% {
      transform: translateX(10px);
      opacity: 1;
    }
    25% {
      transform: translateX(0);
      opacity: 1;
    }
    25% {
        transform: translateX(0);
        opacity: 1;
      }
    35% {
      transform: translateX(-10px);
      opacity: 0;
    }
    80% {
        transform: translateX(-60px);
        opacity:0;
    }
  }

@media(max-width:1200px){
    .foto img{
        width:56%;
